import React from "react";
import $ from 'jquery';
import { Link } from "react-router-dom";

import '../CSS/Nav.css'

function Nav() {

    const serveDivDisplay =() => {
        $(".nav-category-serve-div").css("display", "flex");
    }

    const serveDivNoDisplay =() => {
        $(".nav-category-serve-div").css("display", "none");
    }

    return(
        <div>
            <nav className="navbar bg-light">
                <div className="container-fluid">
                    <form className="d-flex" role="search">
                        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
                        <button className="btn btn-outline-success" type="submit">Search</button>
                    </form>
                    <div className="nav-icons-div">
                        <Link to="/">
                            <i className="bi bi-house"></i>
                        </Link>
                        <Link to="/user/profile">
                            <i className="bi bi-person"></i>
                        </Link>
                        <Link to="/board/write">
                            <i className="bi bi-pencil-square"></i>
                        </Link>
                    </div>
                </div>
            </nav>
            <div className="bg-light nav-category-div"
            onMouseOver={serveDivDisplay} onMouseOut={serveDivNoDisplay}>
                <div>  </div>
                <div>  </div>
                <div>  </div>
                <div>  </div>
            </div>
            <div className="bg-light nav-category-serve-div"
            onMouseOver={serveDivDisplay} onMouseOut={serveDivNoDisplay}>
                {/*  */}
                <div>
                    <ul>
                        <li><p></p></li>
                        <li><p></p></li>
                        <li><p></p></li>
                        <li><p></p></li>
                        <li><p></p></li>
                        <li><p></p></li>
                    </ul>
                </div>
                {/*  */}
                <div className="margin1">
                    <ul>
                        <li><p>5</p></li>
                        <li><p>6</p></li>
                        <li><p>7</p></li>
                    </ul>
                </div>
                {/*  */}
                <div className="margin2">
                    <ul>
                        <li><p>2021</p></li>
                        <li><p>2022</p></li>
                        <li><p>2023</p></li>
                    </ul>
                </div>
                {/*  */}
                <div className="margin3">
                    <ul>
                        <li><p></p></li>
                        <li><p></p></li>
                        <li><p></p></li>
                    </ul>
                </div>
            </div>
        </div>
    )
}

export default Nav;